// ===================== VARIABLES PRINCIPALES =====================
$primary: #00a087;
$primary-dark: #008872;
$dark: #1a4d6c;
$white: #ffffff;
$shadow-strong: 0 20px 60px rgba(0, 0, 0, 0.5);
$shadow-medium: 0 10px 30px rgba(0, 0, 0, 0.3);

/* ===== BOT√ìN PREMIUM RECTANGULAR DERECHA ===== */
.btn-cotizacion {
  position: absolute;
  bottom: 25px;          /* parte baja del carrusel */
  left: 25px;           /* alineado a la derecha */

  background: #004aad;   /* azul profesional/lindo */
  color: #ffffff;

  padding: 16px 40px;
  font-size: 1.2rem;
  font-weight: 700;

  border: none;
  border-radius: 6px;    /* rectangulo con bordes suaves, NO ovalado */

  cursor: pointer;

  box-shadow: 0 10px 22px rgba(0, 0, 0, 0.25);
  transition: all 0.3s ease;
  z-index: 99;
}

/* ===== HOVER ===== */
.btn-cotizacion:hover {
  background: #0060e6;  /* un azul m√°s intenso */
  transform: translateY(-4px);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.35);
}

/* ===== CLICK ===== */
.btn-cotizacion:active {
  transform: translateY(0);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.22);
}

/* ===== RESPONSIVE ===== */
@media (max-width: 768px) {
  .btn-cotizacion {
    padding: 14px 32px;
    font-size: 1.05rem;
    bottom: 18px;
    right: 18px;
  }
}

@media (max-width: 480px) {
  .btn-cotizacion {
    padding: 12px 28px;
    font-size: 1rem;
    bottom: 14px;
    right: 14px;
  }
}




// ===================== CARRUSEL PREMIUM =====================
.premium-carousel {
  position: relative;
  overflow: hidden;
  background: #000;
  margin-bottom: 120px;

  // Gradiente overlay para todas las im√°genes
  .overlay-gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.7) 0%,
      rgba(0, 0, 0, 0.4) 50%,
      rgba(0, 0, 0, 0.2) 100%
    );
    z-index: 1;
  }

  // Imagen principal con efecto Ken Burns
.hero-img {
    width: 100%;
    height: auto !important;
    object-fit: contain !important;
    background-color: #000;
     image-rendering: -webkit-optimize-contrast;
}


  .carousel-item {
    background-color: #000 !important;
}


  // Caption con alineaci√≥n vertical
  .carousel-caption {
    position: absolute;
    top: 0;
    left: 0;
    right: auto;
    bottom: 0;
    height: 100%;
    z-index: 2;
    text-align: left;
    padding: 0 5%;
  }

  // Caja de contenido con glassmorphism premium
  .caption-box {
    max-width: 650px;
    padding: 40px 50px;
    background: rgba(26, 77, 108, 0.15);
    backdrop-filter: blur(20px) saturate(180%);
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 24px;
    box-shadow: $shadow-strong;
    position: relative;
    
    // Borde sutil de brillo
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 24px;
      padding: 1px;
      background: linear-gradient(135deg, rgba(255, 255, 255, 0.3), transparent);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
      pointer-events: none;
    }

    h2 {
      font-size: 3.5rem;
      font-weight: 800;
      color: $white;
      margin-bottom: 0;
      line-height: 1.2;
      text-shadow: 2px 4px 12px rgba(0, 0, 0, 0.6);
      letter-spacing: -0.5px;
    }
  }

  // Indicadores personalizados premium
  .carousel-indicators {
    margin-bottom: 40px;
    gap: 8px;

    button {
      width: 12px;
      height: 12px;
      background-color: rgba(255, 255, 255, 0.4);
      border: 2px solid transparent;
      border-radius: 50%;
      opacity: 1;
      transition: all 0.4s ease;
      margin: 0 4px;

      &.active {
        width: 50px;
        border-radius: 8px;
        background: linear-gradient(90deg, $primary, $white);
        box-shadow: 0 4px 12px rgba(0, 160, 135, 0.6);
      }

      &:hover:not(.active) {
        background-color: rgba(255, 255, 255, 0.6);
        transform: scale(1.2);
      }
    }
  }

  // Controles de navegaci√≥n mejorados
  .carousel-control-prev,
  .carousel-control-next {
    width: auto;
    opacity: 1;
    z-index: 3;
  }

  .carousel-control-prev {
    left: 30px;
  }

  .carousel-control-next {
    right: 30px;
  }

  .control-circle {
    width: 64px;
    height: 64px;
    background: rgba(255, 255, 255, 0.12);
    backdrop-filter: blur(16px);
    border: 1px solid rgba(21, 98, 137, 0.996);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);

    svg {
      color: $white;
      filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
      transition: transform 0.3s ease;
    }

    &:hover {
      background: rgba(255, 255, 255, 0.25);
      transform: scale(1.15);
      box-shadow: 0 12px 32px rgba(0, 0, 0, 0.3);

      svg {
        transform: scale(1.1);
      }
    }

    &:active {
      transform: scale(1.05);
    }
  }
}

// ===================== ANIMACIONES REFINADAS =====================
@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(60px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

// Animaciones escalonadas para el contenido
.carousel-item.active {
  .animate-title {
    animation: fadeUp 0.8s ease-out 0.2s both;
  }
}

// ===================== RESPONSIVE DESIGN =====================
@media (max-width: 1200px) {
  .premium-carousel {
    .caption-box {
      max-width: 550px;
      padding: 35px 45px;

      h2 {
        font-size: 3rem;
      }
    }
  }
}

@media (max-width: 992px) {
