    }

    .carousel-caption {
      padding: 0 4%;
    }

    .caption-box {
      max-width: 480px;
      padding: 30px 40px;

      h2 {
        font-size: 2.5rem;
      }
    }

    .control-circle {
      width: 56px;
      height: 56px;

      svg {
        width: 24px;
        height: 24px;
      }
    }

    .carousel-control-prev {
      left: 20px;
    }

    .carousel-control-next {
      right: 20px;
    }
  }
}

@media (max-width: 768px) {
  .premium-carousel {
    .hero-img {
      height: 65vh;
    }

    .carousel-caption {
      padding: 0 3%;
    }

    .caption-box {
      max-width: 90%;
      padding: 25px 30px;
      border-radius: 18px;

      h2 {
        font-size: 2rem;
      }
    }

    .carousel-indicators {
