}

@media (max-width: 576px) {
  .premium-carousel {
    .hero-img {
      height: 55vh;
    }

    .carousel-caption {
      padding: 0 15px;
    }

    .caption-box {
      padding: 20px 25px;
      border-radius: 16px;

      h2 {
        font-size: 1.6rem;
      }
    }

    .control-circle {
      width: 48px;
