  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 35px 55px;
  justify-items: center;
}

/* ---------- TARJETA DE LOGO ---------- */
.logo-card {
  width: 160px;
  height: 110px;               /* Tama√±o uniforme */
  background: #ffffff;
  padding: 15px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;

  box-shadow: 0 4px 13px rgba(0, 0, 0, 0.08);
  border: 2px solid transparent;

  transition: 
    transform .35s ease,
    border-color .35s ease,
    box-shadow .35s ease;
}

/* Imagen completamente uniforme */
.logo-card img {
  width: 100%;
  height: 100%;
  object-fit: contain;        /* Mantiene el logo perfecto sin deformar */
  filter: grayscale(90%);
  opacity: 0.75;
  transition: 
      transform .35s ease,
      filter .35s ease,
      opacity .35s ease;
}

/* ---------- HOVER ---------- */
.logo-card:hover {
  transform: translateY(-6px);
  border-color: #368a91;         /* Borde verde elegante */
  box-shadow: 0 12px 28px rgba(54, 138, 145, 0.25);
}

.logo-card:hover img {
  transform: scale(1.08);        /* Zoom leve */
  filter: grayscale(0%);
  opacity: 1;
}

