/* ===================== VIDEO PRINCIPAL ===================== */
/* ===================== VIDEO PRINCIPAL (PREMIUM) ===================== */
.main-video-section {
  position: relative;
  width: 100%;
  max-width: 1450px;
  height: 70vh;
  margin: 80px auto;
  overflow: hidden;
  border-radius: 24px;

  /* Borde premium degradado */
  border: 3px solid transparent;
  background: linear-gradient(#000, #000) padding-box,
              linear-gradient(135deg, #1a4d6c, #2c8ead, #29a745) border-box;

  box-shadow: 0 25px 70px rgba(0, 0, 0, 0.45);
  transition: all 0.4s ease-in-out;
}

/* Hover Cinem√°tico */
.main-video-section:hover {
  transform: translateY(-6px);
  box-shadow: 0 35px 90px rgba(0, 0, 0, 0.6);
}

/* Estilo del video */
.main-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.55s ease, filter 0.55s ease;
}

/* Zoom elegante al hover */
.main-video-section:hover .main-video {
  transform: scale(1.06);
  filter: brightness(1.15) contrast(1.05);
}

/* Overlay Cinem√°tico */
.main-video-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.75));
  display: flex;
  align-items: flex-end;
  padding: 55px;
  pointer-events: none;
  opacity: 1;
  transition: opacity 0.4s ease;
}

/* ========== T√çTULO PREMIUM ========== */
.main-video-text {
  display: flex;
  align-items: center;
  gap: 14px;

  color: #fff;
  font-size: 3.2rem;
  font-weight: 900;
  margin: 0;

  text-shadow: 4px 4px 18px rgba(0, 0, 0, 0.85);
  animation: fadeInUp 1.1s ease-out both;
  transition: transform 0.4s ease, opacity 0.4s ease;
}

/* Barra | corporativa */
.main-video-text::before {
  content: "";
  width: 6px;
  height: 48px;
  background: #2c8ead;
  border-radius: 4px;
  display: inline-block;
}

/* Texto se desvanece en hover */
.main-video-section:hover .main-video-text {
  opacity: 0;
  transform: translateY(25px);
}

/* Animaci√≥n inicial */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(35px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===================== RESPONSIVE ===================== */
@media (max-width: 992px) {
  .main-video-section {
    height: 55vh;
    margin: 60px 25px;
  }

  .main-video-text {
    font-size: 2.6rem;
  }

  .main-video-text::before {
    height: 40px;
  }
}

@media (max-width: 600px) {
  .main-video-section {
    height: 40vh;
  }

  .main-video-text {
    font-size: 1.9rem;
  }

  .main-video-overlay {
    padding: 30px;
  }

  .main-video-text::before {
    height: 30px;
    width: 4px;
  }
}






