/* Contenedor general */
.ofertas-home {
  padding: 70px 25px;
  max-width: 1400px;
  margin: 0 auto;
  background: #f8fafc;
}

/* HEADER */
.section-header {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

/* Barra vertical | */
.section-header .header-bar {
  width: 6px;
  height: 48px;
  background: linear-gradient(180deg, #2c8ead, #1a4d6c);
  border-radius: 6px;
  margin-right: 18px;
}

/* T√≠tulo */
.section-header h2 {
  font-size: 3rem;
  font-weight: 800;
  color: #1a4d6c;
  margin: 0;
  letter-spacing: 0.5px;
}

/* Subt√≠tulo */
.section-subtitle {
  color: #3a5a73;
  font-size: 1.3rem;
  margin-bottom: 50px;
  margin-left: 90px;
  max-width: 700px;
  line-height: 1.4;
}

/* GRID */
.productos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
  gap: 40px;
}

/* TARJETA */
.producto-card {
  position: relative;
  background: #ffffff;
  border: 2px solid #cce4ef;
  border-radius: 18px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.4s ease;
}

/* Hover tarjeta */
.producto-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 18px 36px rgba(26, 77, 108, 0.25);
  border-color: #1a4d6c;
}

/* Imagen */
.producto-card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  transition: transform 0.45s ease;
}

.producto-card:hover img {
  transform: scale(1.07);
}

/* INFO */
.producto-info {
  padding: 28px 24px;
}

.producto-info h3 {
  font-size: 1.6rem;
  font-weight: 700;
  color: #164d6c;
  margin-bottom: 12px;
}

.producto-info p {
  color: #3a5a73;
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 18px;
}

/* BOT√ìN */
.btn-ver-detalles {
  width: 100%;
  padding: 14px 0;
  font-size: 1.15rem;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  background: linear-gradient(90deg, #2c8ead, #1a4d6c);
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0;
  transform: translateY(10px);
  pointer-events: none;
}

.producto-card:hover .btn-ver-detalles {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
  box-shadow: 0 10px 25px rgba(44, 142, 173, 0.5);
}

.btn-ver-detalles:hover {
  background: linear-gradient(90deg, #1a4d6c, #164d6c);
}

/* ETIQUETA DISPONIBLE */
.etiqueta-disponible {
  position: absolute;
  top: 15px;
  left: 15px;
  background: linear-gradient(135deg, #e42b2b, #f60808);
  color: #ffffff;
  padding: 8px 18px;
  font-size: 0.85rem;
  font-weight: 700;
  border-radius: 20px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 12px rgba(26, 77, 108, 0.35);
}

/*============ RESPONSIVE ============*/

/* Tablets */
@media (max-width: 992px) {
  .section-header h2 {
    font-size: 2.6rem;
  }

  .section-subtitle {
    margin-left: 0;
    text-align: left;
    font-size: 1.2rem;
  }

  .productos-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 30px;
  }
}

/* M√≥viles */
@media (max-width: 576px) {
  .section-header {
    flex-direction: row;
    align-items: center;
  }

  .section-header .header-bar {
    height: 40px;
  }

  .section-header h2 {
    font-size: 2.2rem;
  }

  .section-subtitle {
    font-size: 1.05rem;
    margin-left: 0;
    margin-bottom: 35px;
  }

  .productos-grid {
    grid-template-columns: 1fr;
